*,
*::before,
*::after {
    margin: 0;
}

html {
    font-size: 62.5%;
    box-sizing: border-box;
    height: 100vh;
    width: 100%;
}

body {
    font-family: sans-serif;
    font-size: 1.6rem;
}

*:focus {
    outline: none;
}
.sign_container{width:100%;height:100%;display:grid;grid-template-columns:.25fr 1fr 1fr .25fr;grid-template-rows:100vh;grid-column-gap:1rem;justify-items:center;align-items:center;background-image:linear-gradient(80deg, orange, #c70039);color:white;font-family:'Courier New', Courier, monospace}

.input__container{margin:1.5rem 0}.input__container-input{padding:1rem 1.5rem;display:block;width:35rem;height:2rem;font-size:1.5rem;border-radius:2rem;outline:none;color:darkgrey;margin-top:.5rem;transition:all .3s}.input__container-input:focus{border:2px solid #1dd3bd;box-shadow:1px 1px .5rem #1dd3bd, -1px -1px .5rem #1dd3bd}.input__container label{padding:1rem;font-size:1rem;letter-spacing:1.2px;text-transform:uppercase;font-weight:bold;color:#fff}

.signIn,.signUp{display:flex;flex-direction:column;align-items:center;justify-content:center}.signIn__form,.signUp__form{margin:1rem 0}.signIn__form-h4,.signUp__form-h4{font-size:1.1rem}.signIn{grid-column:2 / 3}.signUp{grid-column:3 / 4}.form__btn{margin-top:1rem;width:100%;display:flex;flex-direction:row;align-items:center;justify-content:center}

.sign__btn-button{display:inline-block;font-family:'Courier New', Courier, monospace;font-size:1.6rem;color:white;background-color:#3f4441;box-shadow:0.3rem 0.3rem 0.5rem rgba(0,0,0,0.4);font-weight:bolder;width:18rem;padding:1.5rem 1.8rem;border-radius:2rem;margin:1rem;cursor:pointer}@media only screen and (max-width: 69.4em){.sign__btn-button{padding:.7rem 1.5rem}}.sign__btn-button:hover{background-color:#323232}.sign__btn-button:active{background-color:#3f4441}

.homepage__container{width:100%;height:100vh;display:grid;grid-template-columns:repeat(2, -webkit-min-content) minmax(-webkit-max-content, 100vw) -webkit-min-content;grid-template-columns:repeat(2, min-content) minmax(max-content, 100vw) min-content;grid-template-rows:-webkit-max-content minmax(-webkit-max-content, 100vh);grid-template-rows:max-content minmax(max-content, 100vh);background:linear-gradient(to right, rgba(255,66,127,0.7), rgba(121,215,15,0.7)),url(/static/media/brain.1b631f8e.jpg);background-size:cover;background-repeat:no-repeat;background-position:center}

.navbar_container{display:flex;align-items:center;flex-direction:row;justify-content:flex-end;align-items:center;background-color:rgba(0,0,255,0.411);padding-top:1rem;padding-bottom:1rem;width:100%;grid-column:1 / 6;grid-row:1 / 2}.navbar_container .navbar_icon{position:relative;margin-right:3rem;width:3.5rem;height:3.5rem;border-radius:50%;cursor:pointer}.navbar_container .navbar_icon .nav__user{position:absolute;display:flex;align-items:center;justify-content:center;height:100%;width:100%;border-radius:50%;font-family:Georgia, 'Times New Roman', Times, serif;color:white;background-color:#14b1ab;text-align:center}.navbar_container .navbar_icon .nav__user img{width:2.8rem;height:2.8rem;border-radius:50%;background-color:#f2f7f5}.navbar_container .navbar_icon .nav__user h5{display:inline-block;font-size:1.8rem}

.navbar_detail{position:relative;display:flex;width:0;height:auto;margin-right:.5rem;border-radius:1rem}.navbar_detail-signIn,.navbar_detail-signOut{display:flex;align-items:center;justify-content:center;width:6rem;height:2rem;padding:.5rem 1rem;border:none;color:white;background-color:rgba(32,248,230,0.8);font-family:'Courier New', Courier, monospace;font-size:1.2rem;cursor:pointer;transition:all .1s;-webkit-animation:appears ease-in-out .1s;animation:appears ease-in-out .1s;-webkit-backface-visibility:hidden;backface-visibility:hidden;border-radius:1rem}.navbar_detail-signIn a,.navbar_detail-signIn a:active,.navbar_detail-signIn a:visited,.navbar_detail-signOut a,.navbar_detail-signOut a:active,.navbar_detail-signOut a:visited{text-decoration:none;color:white}.navbar_detail-signIn:hover,.navbar_detail-signOut:hover{box-shadow:1px 1px 3px #f3f4f5, -1px -1px 3px #f8fafc}@-webkit-keyframes appears{0%{width:0;font-size:0}100%{width:auto;font-size:1.2rem}}@keyframes appears{0%{width:0;font-size:0}100%{width:auto;font-size:1.2rem}}

.footer_container{font-family:Georgia, 'Times New Roman', Times, serif;grid-column:1 / 6;padding:1rem auto .5rem;display:flex;align-items:flex-end;justify-content:center}.footer_container-content{color:white;letter-spacing:2px;display:inline-block}.footer_container-content .copyright{display:inline-block;width:auto;font-size:1.6rem;margin-right:2px}.footer_container-content .footer-name{display:inline-block;width:auto}.footer_container-content:hover .footer-name{background-color:#17161b;box-shadow:2px 2px 5px rgba(0,0,0,0.8);transform:scale(1.1) skewX(-10deg)}

.sidebar__container{width:10rem;height:100%;display:flex;flex-direction:column;align-items:center;grid-column:1/2;grid-row:2 / 3}

.sidebar__item-container{height:3rem;width:80%;margin-top:4rem;cursor:pointer;border-radius:1rem;box-shadow:0.3rem 0.3rem 0.5rem rgba(51,51,51,0.7)}.sidebar__item-container:hover .sidebar__item{transform:skewX(15deg)}.sidebar__item-container:active{background-color:rgba(49,49,49,0.2)}.sidebar__item-container .sidebar__item{width:100%;height:100%;display:flex;align-items:center;justify-content:center}.sidebar__item-container .sidebar__item-name{font-family:'Courier New', Courier, monospace;color:white;text-transform:uppercase;font-size:1.6rem;font-weight:bold;letter-spacing:1.1px}

.mainContent__container{grid-row:2 / 3;grid-column:3 / 4}

.learn__container{position:relative;height:100%;display:none;align-items:center;justify-content:center;max-width:90vw}.learn__container-num{display:flex;flex-wrap:wrap;justify-content:center;align-items:center;height:85%;width:90%}

.card__container{position:relative;height:15rem;width:13rem;margin:-4rem 3rem 2rem;transition:all .6s;-webkit-animation:appear ease-in .5s;animation:appear ease-in .5s}.card__container:hover{transform:scale(1.2)}.card__container:hover .frontCard__container{transform:rotateY(180deg)}.card__container:hover .backCard__container{transform:rotateY(0)}@-webkit-keyframes appear{0%{opacity:0;transform:translateY(-2rem)}100%{opacity:1;transform:translate(0)}}@keyframes appear{0%{opacity:0;transform:translateY(-2rem)}100%{opacity:1;transform:translate(0)}}

.frontCard__container{height:100%;width:100%;background-color:chocolate;-webkit-backface-visibility:hidden;backface-visibility:hidden;perspective:350rem;transition:all 1s;display:flex;align-items:center;justify-content:center;box-shadow:0.3rem 0.3rem 0.5rem #4e4d4d}.frontCard__container .frontCard_detail{color:white;display:inline-block}

.backCard__container{position:absolute;width:100%;height:100%;top:0;left:0;transition:all 1s;display:flex;justify-content:center;align-items:center;-webkit-backface-visibility:hidden;backface-visibility:hidden;transform:rotateY(-180deg);background-color:white;perspective:1000rem;box-shadow:0.3rem 0.3rem 0.5rem rgba(121,114,114,0.377)}.backCard__container img{height:10rem;width:10rem}@-webkit-keyframes comeout{0%{opacity:0}100%{opacity:1}}@keyframes comeout{0%{opacity:0}100%{opacity:1}}

.number__container{position:absolute;height:5rem;width:100%;bottom:2rem;left:0;display:flex;align-items:center;justify-content:center}.number__container .number__num{height:4rem;width:4rem;border-radius:50%;margin:1rem;display:flex;align-items:center;justify-content:center;cursor:pointer;background-image:linear-gradient(40deg, #c70039, orange);box-shadow:inset 1px 2px 5px white,3px 3px 5px rgba(0,0,0,0.5);transition:all .3s}.number__container .number__num:hover{transform:scale(1.2)}.number__container .number__num .number{font-size:2rem;-webkit-background-clip:text;background-clip:text;color:white;font-weight:bold}

.test__container{position:relative;height:100%;display:flex;align-items:center;flex-direction:column;max-width:90vw}.test__container-card{height:50%;width:100%;display:flex;align-items:center;justify-content:center}.test__container-card .test__container-backCard{position:relative;height:15rem;width:13rem;margin:0 3rem}.test__container-card .test__container-backCard .backCard__container-test{position:absolute;width:100%;height:100%;top:0;left:0;transition:all 1s;display:flex;justify-content:center;background-color:white;align-items:center;box-shadow:0.5rem 0.5rem 1rem rgba(58,56,56,0.377);transition:all .5s;-webkit-animation:comeout ease-in 1s;animation:comeout ease-in 1s}.test__container-card .test__container-backCard .backCard__container-test img{height:10rem;width:10rem}.test__container-start{position:absolute;width:10rem;height:10rem;display:flex;justify-content:center;align-items:center;background-color:orchid;border-radius:50%;transition:all .5s}.test__container-form{height:50%;width:100%;display:flex;justify-content:space-around;flex-direction:column;align-items:center}.test__container .test__result{color:green;text-shadow:1px 1px 5px white}.test__container .test__form{padding:2rem 3rem;display:flex;justify-content:space-around;align-items:center;background:radial-gradient(circle farthest-side at 0% 50%, #fb1 23.5%, rgba(240,166,17,0) 0) 21px 30px,radial-gradient(circle farthest-side at 0% 50%, #B71 24%, rgba(240,166,17,0) 0) 19px 30px,linear-gradient(#fb1 14%, rgba(240,166,17,0) 0, rgba(240,166,17,0) 85%, #fb1 0) 0 0,linear-gradient(150deg, #fb1 24%, #B71 0, #B71 26%, rgba(240,166,17,0) 0, rgba(240,166,17,0) 74%, #B71 0, #B71 76%, #fb1 0) 0 0,linear-gradient(30deg, #fb1 24%, #B71 0, #B71 26%, rgba(240,166,17,0) 0, rgba(240,166,17,0) 74%, #B71 0, #B71 76%, #fb1 0) 0 0,linear-gradient(90deg, #B71 2%, #fb1 0, #fb1 98%, #B71 0%) 0 0 #fb1;background-size:40px 60px}.test__container .test__form-input{padding:.8rem 2.5rem;font-size:2rem;color:green;margin-right:2rem;border-radius:10rem;border:none;margin-right:1rem;margin-left:1rem}.test__container .test__form-btn{padding:1rem 2rem;border-radius:2rem;cursor:pointer;background-color:#0a97b0;box-shadow:inset 2px 2px .5rem white, inset -3px -3px .5rem #1b6ca8;color:white;font-size:1.5rem;font-weight:bold}.test__container .test__form-btn.start{padding:1rem 1rem;background-color:#049c04;color:white;font-size:2rem;border:none;font-family:Georgia, 'Times New Roman', Times, serif;text-transform:uppercase;height:10rem;width:10rem;border-radius:50%;box-shadow:inset 5px 5px 1rem white, inset -3px -3px 1rem #0e5505}.test__container .test__form-btn.start:active{background-color:#036a03;transform:translateY(-2px)}@-webkit-keyframes turnaround{0%{height:10rem;width:10rem}50%{box-shadow:1rem 1rem 10rem orangered, -1rem -1rem 10rem orangered;top:11rem}100%{height:10rem;width:10rem}}@keyframes turnaround{0%{height:10rem;width:10rem}50%{box-shadow:1rem 1rem 10rem orangered, -1rem -1rem 10rem orangered;top:11rem}100%{height:10rem;width:10rem}}


